						</Row>
						<a class="diy_button" @click="diy"></a>
					</div>
				</Card>
			</div>
			<div style="padding: 50px;"></div>
		</div>
		<!-- <Modal v-model="modal" fullscreen title="DIY"> -->
		<Drawer title="DIY" v-model="modal" width="720" :mask-closable="false">
			<Form :model="form" :label-width="80">
				<FormItem label="">
					<RadioGroup v-model="form.address">
						<Radio :disabled="item.disabled" v-for="(item, index) in addressList" :key="index" :label="item.name"></Radio>
					</RadioGroup>
				</FormItem>
				<FormItem label="">
					<Select v-model="form.planDegree" style="width:300px">
						<Option v-for="item in planDegreeList" :value="item.text" :key="item.text">{{ item.text }}</Option>
					</Select>
				</FormItem>
				<FormItem label="">
					<Select v-model="form.studyTime" style="width:300px">
						<Option v-for="item in studyTimeList" :value="item.text" :key="item.text">{{ item.text }}</Option>
					</Select>
				</FormItem>
				<FormItem label="">
					<input v-model="form.major" style="width: 300px" />
				</FormItem>
				<FormItem label="">
					<Select v-model="form.outsideTime" style="width:300px">
						<Option v-for="item in outsideTimeList" :value="item.text" :key="item.text">{{ item.text }}</Option>
					</Select>
				</FormItem>
				<FormItem label="">
					<input v-model="form.university" style="width: 300px" />
				</FormItem>
				<FormItem label="">
					<input v-model="form.score" placeholder="5" style="width: 300px" />
				</FormItem>
				<FormItem label="">
					<input v-model="form.tel" placeholder="5" style="width: 300px" />
				</FormItem>


				<FormItem>
					<Button type="primary" @click="submit" :disabled="button">DIY</Button>
					<Button type="primary" @click="toresult" :disabled="!button"></Button>
				</FormItem>
			</Form>

		</Drawer>
		<!-- </Modal> -->

	</div>
</template>

<script>
	export default {
		data() {
			return {
				button: false,
				modal: false,
				bottom: this.$global.bottom,
				addressList: [{
						name: '',
						checked: false,
						disabled: false
					},
					{
						name: '',
						checked: false,
						disabled: false
					},
					{
						name: '',
						checked: false,
						disabled: false
					},
					{
						name: '',
						checked: false,
						disabled: false
					},
					{
						name: '',
						checked: false,
						disabled: false
					}
				],
				planDegreeList: [{
					text: ''
				}, {
					text: ''
				}, {
					text: ''
				}],
				studyTimeList: [{
					text: 1
				}, {
					text: 2
				}],
				outsideTimeList: [{
					text: ''
				}, {
					text: ''
				}, {
					text: ''
				}, {
					text: ''
				}],
				form: {
					// address: [],
					address: '',
					planDegree: '',
					studyTime: 2,
					major: '',
					outsideTime: '',
					university: '',
					score: '4.9',
					tel: '1234567890'
				},
				columns1: [{
						title: '',
						key: 'name',
						className: 'pk'
					},
					{
						title: '',
						key: 'us',
						className: 'us',
						width: 600
					},
					{
						title: '',
						key: 'it',
						className: 'it'
					}
				],
				data1: [{
					name: '',
					us: '40000',
					it: '5W - 10W'
				}, {
					name: '',
					us: '',
					it: ' '
				}, {
					name: '',
					us: '',
					it: ''
				}],
				id: ''
			}
		},
		methods: {
			toresult() {
				this.$router.push({
					path: '/diy_result'
				});
			},
			diy() {
				if (!localStorage.getItem("user")) {
					localStorage.setItem("to", "/diy_index")
					this.$router.push({
						path: "/login"
					});
				}
				this.id = localStorage.getItem("id");
				this.$api.getRequest(this.$url.INTERFACES.applyOneByUser + this.id).then(res => {
					if (res.code == 200) {
						this.button = true;
					}
				})
				this.modal = true
			},
			submit() {
				var data = {
					userId: localStorage.getItem("id"),
					address: this.form.address,
					degree: this.form.planDegree,
					years: this.form.studyTime,
					major: this.form.major,
					outsideDate: this.form.outsideTime,
					university: this.form.university,
					score: this.form.score,
					tel: this.form.tel
				};
				console.log(data)
				this.$api.postRequest(this.$url.INTERFACES.applySave, data).then(res => {
					this.$Message['success']({
						background: true,
						content: ''
					});
					this.$router.push({
						path: '/diy_result'
					});
				})
			},
		}
	}
</script>

